body{
  background-color: #fff;
}
.App {
  margin: 15px 0;
  background-color: #fff;
}
.App-header {
  background-color: transparent;
  height: 100px;
  padding: 20px;
  color: white;
}
.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.Images{
  margin: 15px 5px;
  margin: 15px 5px;
  margin-top: 100px;
  padding: 15px;
}
.clicked-image-container {
  position: relative;
  width: 50%;
  overflow: hidden;
  height: 700px;
  display: inline-block;
}
.dummy {
  padding-top: 55%;
}
.img-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; 
}
.img-container img{
  height: 75%;
  -webkit-box-shadow: 0 0 40px #000;
          box-shadow: 0 0 40px #000;
}
.rel-image-container{
    display: inline-block;
    height: 650px;
    position: absolute;
    left: 10;
    margin-left: 25px;
    border-left: 1px solid #000;
    padding-top: 85px;
    padding-left: 50px;
}
.clicked-item{
  width: 100%;
  background-color: #1a1f26;
  color: white;
  position: fixed;
  top: 15%;
  left: 0;
}
.navigate{
  margin-top: 15%;
  height: 20px;
  padding: 60px 5px;
  background-color: #173753;
  cursor: pointer;
  color: white;
}
.navigate:hover{
  background-color: #173733;
}
.left-arrow{
  float: left;
}
.right-arrow{
  float: right;
}
.close-button{
  float: right;
  padding: 0px;
  position: absolute;
  right: 0px;
  cursor: pointer;
  top: 5px;
}
.no-margin{
  margin: 0;
}
.resol-of-img{
  bottom: 0;
  position: absolute;
  text-align: center;
  display: block;
  left: 45%;
}
.visit-page{
  margin-top: 15px;
  padding: 10px;
  background-color:  darkslategray;
  border: none;
  color: white;
  cursor: pointer;
}
.visit-page a{
  color: white;
  text-decoration: none;
  margin-left: 5px;
}
.visit-page a:hover{
  text-decoration: underline;
}
.Seacrh-Box{
  position: fixed;
  top: 30px;
  width: 100%;
  text-align: center;
}
.Search-Label{
    padding: 15px;
    background-color: lightblue;
    top: -1px;
    margin-top: 6px;
    left: 1px;
    position: relative;
    border: 1px solid #ababaa;
}


